import React from 'react';
import connect from '@vkontakte/vkui-connect';
import {
	FixedLayout, Textarea, CellButton, Input, FormLayout, Tooltip, PullToRefresh, Gallery, View, Panel,
	PanelHeader, Cell, Tabs, Search, List, TabsItem, Group, ListItem, Alert, Avatar, ScreenSpinner, Epic, Tabbar,
	HeaderButton, TabbarItem, Div, Button, Switch
} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import './css/my.css';
import Icon28Newsfeed from '@vkontakte/icons/dist/28/newsfeed';
import Icon28KeyboardBotsOutline from '@vkontakte/icons/dist/28/keyboard_bots_outline';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28User from '@vkontakte/icons/dist/28/user';
import Icon24Settings from '@vkontakte/icons/dist/24/settings';
import Icon24Users from '@vkontakte/icons/dist/24/users';
import Icon24Services from '@vkontakte/icons/dist/24/services';
