	setTitle(chat) {
		if (this.state.title === "" || this.state.title === " ") {
			this.setState({
				popout:
					<Alert
						actions={[{
							title: 'Закрыть',
							autoclose: true,
							style: 'cancel'
						}]}
						onClose={this.closePopout}
					>
						Укажите новое название для беседы
				</Alert>
			})
			return
		}
		if (this.state.title === this.state.currentChat.title) {
			this.setState({
				popout:
					<Alert
						actions={[{
							title: 'Закрыть',
							autoclose: true,
							style: 'cancel'
						}]}
						onClose={this.closePopout}
					>
						Название не должно совпадать с настоящим
				</Alert>
			})
			return
		}
		fetch(`${this.state.host_api}/chats/${chat.chat_id}/rename_title?${window.location.search.replace('?', '')}&title=${this.state.title}`)
			.then(res => res.json())
			.then(res => {
				if (res.items.find((e) => e.error)) {
					this.setState({
						popout:
							<Alert
								actions={[{
									title: 'Закрыть',
									autoclose: true,
									style: 'cancel'
								}]}
								onClose={this.closePopout}
							>
								Недостаточно прав, чтобы изменить название беседы
					</Alert>
					});
					return
				}
				this.setState({
					popout:
						<Alert
							actions={[{
								title: 'Закрыть',
								autoclose: true,
								style: 'cancel'
							}]}
							onClose={this.closePopout}
						>
							Название беседы изменено на <b>{this.state.title}</b>
						</Alert>
				});
				this.state.currentChat.title = this.state.title
				this.setState({ activePanel: 'currentChat', activeStory: 'manage', title: '' })
			})
	}
